import VisualComponent from "jx/core/comps/VisualComponent";
/**
 *
 */
export default class Shape extends VisualComponent {
    /**
    * @param {object} dataObject
    * @param {string} dataObject.shapeType - Type of shape create (default="Rectangle"). Only Rectangle available today, used principally for visuel during dev or placements zones.
    * @param {color} dataObject.color - color of the shape
    *
    * @example
    * this.cc({type: "Shape", shapeType: "Rectangle", x: 0, y: 0, width: 200, height: 100, color: "#00FF00"});
     */
    constructor(dataObject) {
        super(Object.assign({
            shapeType: "rectangle",
            color: "#000000"
        }, dataObject));
        this.extendsFrom("Shape");
    }
}
